@use 'base/mixins' as m;
@use 'base/variables' as v;

.title-header{
    background: linear-gradient(to right, rgba(68, 1, 29, 0.8), rgba(68, 1, 29, 0.5) 33%, rgba(68, 1, 29, 0.5)  66%, rgba(68, 1, 29, 0.8));
    padding: 1rem;
    

    .box-header{
        display: flex;
        justify-content: space-between;
        align-items: center;

        h1{
            color: v.$blanco;
            text-align: left;
            margin: 0 2rem;
            text-shadow: 2px 2px v.$morado;
            line-height: 3rem;
            @include m.tablet {
                margin: 0 4rem;
                line-height: 4rem;
                font-size: 5.5rem;
                
            }
        }

        .menu-bars{
            color:v.$blanco;
            position: relative;
            background: transparent;
            min-height: 50px;            
            @include m.tablet {
                display:none;
                
            }

            a{
                display:block;
                color:v.$morado;
                text-decoration: none;
                padding: 10px 20px;
                border-bottom: 2px, solid, v.$morado;
                border-radius: 5px;
                font-weight: bold;
                background-color: v.$blanco;
                
            }

            input[type="checkbox"], .fa-bars, .fa-times{
                position:absolute;
                box-sizing: border-box;
                margin:0;
                padding: 0;
                right: 10px;
                top: 0;
                width: 50px;
                height:50px;
            }

            .fa-bars, .fa-times{
                font-size: 50px;
                pointer-events: none;
            }

            input[type="checkbox"]{
                opacity:0;
            }

            nav{
                display:none;
                position:absolute;
                top: 30px;
                right: 10px;

                li{
                    list-style:none;
                }
            }

            input:checked ~ nav {
                display: block;
              }

              input:not(:checked) ~ nav{
                  display:none;
              }

              input:checked ~ .fa-bars {
                display: none;
              }

              input:not(:checked) ~ .fa-times {
                display: none;
              }



        }
    }



    .menu-principal{
        display:none;
        
        
        

        @include m.tablet {
            text-align:center;
            display: flex;
            justify-content: space-between;
            padding: 2rem 4rem 0 4rem;
            
        }
    

    }

}

.backimg{
    background-image: url("../img/head.webp");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    @include m.tablet {
        background-image: url("../img/headTable.webp");
    }
}

.boton{
    @include m.boton (v.$blanco, v.$morado );
}

.redes-sociales{
    text-align:center;
    padding-top: 1rem;
    
        i{
            text-decoration: none;
            color: v.$blanco;
            text-shadow: 2px 2px v.$morado;
            cursor: pointer;
        }
}