/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  min-width: 350px;
  display: flex;
  flex-direction: column;
  background-color: rgb(252, 239, 242);
  color: #5a0231;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Cinzel", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor_small {
  width: 80%;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Rancho", cursive;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #5a0231;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
  text-align: center;
}

p {
  font-family: "Cinzel", sans-serif;
  color: #FFFFFF;
  font-size: 2rem;
}

.title-header {
  background: linear-gradient(to right, rgba(68, 1, 29, 0.8), rgba(68, 1, 29, 0.5) 33%, rgba(68, 1, 29, 0.5) 66%, rgba(68, 1, 29, 0.8));
  padding: 1rem;
}
.title-header .box-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.title-header .box-header h1 {
  color: #FFFFFF;
  text-align: left;
  margin: 0 2rem;
  text-shadow: 2px 2px #5a0231;
  line-height: 3rem;
}
@media (min-width: 768px) {
  .title-header .box-header h1 {
    margin: 0 4rem;
    line-height: 4rem;
    font-size: 5.5rem;
  }
}
.title-header .box-header .menu-bars {
  color: #FFFFFF;
  position: relative;
  background: transparent;
  min-height: 50px;
}
@media (min-width: 768px) {
  .title-header .box-header .menu-bars {
    display: none;
  }
}
.title-header .box-header .menu-bars a {
  display: block;
  color: #5a0231;
  text-decoration: none;
  padding: 10px 20px;
  border-bottom: 2px, solid, #5a0231;
  border-radius: 5px;
  font-weight: bold;
  background-color: #FFFFFF;
}
.title-header .box-header .menu-bars input[type=checkbox], .title-header .box-header .menu-bars .fa-bars, .title-header .box-header .menu-bars .fa-times {
  position: absolute;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  right: 10px;
  top: 0;
  width: 50px;
  height: 50px;
}
.title-header .box-header .menu-bars .fa-bars, .title-header .box-header .menu-bars .fa-times {
  font-size: 50px;
  pointer-events: none;
}
.title-header .box-header .menu-bars input[type=checkbox] {
  opacity: 0;
}
.title-header .box-header .menu-bars nav {
  display: none;
  position: absolute;
  top: 30px;
  right: 10px;
}
.title-header .box-header .menu-bars nav li {
  list-style: none;
}
.title-header .box-header .menu-bars input:checked ~ nav {
  display: block;
}
.title-header .box-header .menu-bars input:not(:checked) ~ nav {
  display: none;
}
.title-header .box-header .menu-bars input:checked ~ .fa-bars {
  display: none;
}
.title-header .box-header .menu-bars input:not(:checked) ~ .fa-times {
  display: none;
}
.title-header .menu-principal {
  display: none;
}
@media (min-width: 768px) {
  .title-header .menu-principal {
    text-align: center;
    display: flex;
    justify-content: space-between;
    padding: 2rem 4rem 0 4rem;
  }
}

.backimg {
  background-image: url("../img/head.webp");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .backimg {
    background-image: url("../img/headTable.webp");
  }
}

.boton {
  background-color: #FFFFFF;
  padding: 1rem 2rem;
  color: #5a0231;
  margin-top: 1rem;
  font-size: 1.5rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 1.5rem 0;
  box-shadow: 2px 2px #5a0231;
  border-radius: 5px;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #e6e6e6;
  cursor: pointer;
}

.redes-sociales {
  text-align: center;
  padding-top: 1rem;
}
.redes-sociales i {
  text-decoration: none;
  color: #FFFFFF;
  text-shadow: 2px 2px #5a0231;
  cursor: pointer;
}

footer {
  background-color: #5a0231;
  padding: 1rem;
}

.redes-sociales {
  text-align: center;
}
.redes-sociales i {
  text-decoration: none;
  color: #FFFFFF;
  text-shadow: 2px 2px #5a0231;
  cursor: pointer;
}

.organiza {
  display: block;
  text-align: center;
  margin-bottom: 4rem;
}
.organiza span {
  font-weight: bold;
}
@media (min-width: 768px) {
  .organiza {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #FFFFFF;
    padding: 1rem;
  }
  .organiza p {
    margin: 1rem;
  }
  .organiza p:first-child {
    padding-left: 2rem;
  }
  .organiza P:last-child {
    padding-right: 2rem;
  }
}